import React from 'react';
import { HashRouter } from 'react-router-dom';

// This is a simple wrapper component that returns a HashRouter for actual use
// and can be transformed into a BrowserRouter-like component for react-snap
const ReactSnapHashRouter: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  // Check if we're in a react-snap environment
  const isSnapEnvironment = 
    typeof navigator !== 'undefined' && 
    navigator.userAgent && 
    navigator.userAgent.includes('ReactSnap');

  if (isSnapEnvironment) {
    // For react-snap, use a simple wrapper that mimics BrowserRouter
    return <div className="router-wrapper">{children}</div>;
  }

  // For normal usage, use HashRouter
  return <HashRouter>{children}</HashRouter>;
};

export default ReactSnapHashRouter;