import React from 'react';
import { Routes, Route } from 'react-router-dom';
import ReactSnapHashRouter from './ReactSnapHashRouter'; // Import the custom router
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';
import HomePage from './pages/HomePage';
import HousePlansPage from './pages/HousePlansPage';
import StylesPage from './pages/StylesPage'; // Make sure this is imported
import BlogPage from './pages/BlogPage';
import BlogPostPage from './pages/BlogPostPage';
import NotFoundPage from './pages/NotFoundPage';
import { ThemeProvider } from 'styled-components';
import theme from './styles/theme';
import { GlobalStyles } from './styles/GlobalStyles';
import { I18nextProvider } from 'react-i18next';
import i18n from './i18n/config';

function App() {
  return (
    <I18nextProvider i18n={i18n}>
      <ThemeProvider theme={theme as any}>
        <GlobalStyles />
        <ReactSnapHashRouter> {/* Replace HashRouter with ReactSnapHashRouter */}
          <Header />
          <main>
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/house-plans" element={<HousePlansPage />} />
              <Route path="/styles" element={<StylesPage />} /> {/* Make sure this route is included */}
              <Route path="/blog" element={<BlogPage />} />
              <Route path="/blog/:slug" element={<BlogPostPage />} />
              <Route path="*" element={<NotFoundPage />} />
            </Routes>
          </main>
          <Footer />
        </ReactSnapHashRouter>
      </ThemeProvider>
    </I18nextProvider>
  );
}

export default App;
